
import React from 'react';
import { faPlay, faChevronDown } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import './Section.css';

const AnimeListCovers = (props) => {

    const gFilters = ['Ação', 'Artes Marciais', 'Fantasia', 'Comédia'];

    const AnimeListInfo = props.animeData; // recupera os dados passados pela animeData Object


    const getAnimeList = (animesList, genreFilter) => {
        const filteredList = [];
        animesList.forEach(anime => {
            anime.genre.split(',').forEach(genre => {
                if (genre === genreFilter) {
                    filteredList.push(anime)
                }
            })
        });

        return filteredList;

    }

    let AnimesDiv = gFilters.map(genre =>{

        return getAnimeList(AnimeListInfo, genre)
        
    })


    AnimesDiv = AnimesDiv.map((item, index) => { //retorna a lista com as capas em divs html

        item = item.map((row) => {
    
            return (
                <div key={row.id} className="item--info">
                    <img className="anime--cover" src={row.seasons[0].desk + row.seasons[0].deskCover} alt={row.title}/>
                    <div className="anime--info">
                        <div className="title--info">{row.title}</div>
                        <div className="genres--info">{
                            row.genre.split(",").map((genre,gIndex) => { return <div key={gIndex}>{genre}</div> })
                        }</div>
                        <div className="ratio--info">{row.ratio}</div>
                        <div className="launch--info">{row.launch}</div>
                        <div className="control--buttons">
                            <a onClick={()=> props.playAnime(row)} className="play-button" href="#" title="Assistir"><FontAwesomeIcon className="play--icon" icon={faPlay} /></a>
                        </div>
                    </div>
                </div>
            )
        })

        
        return (
            <div key={index} className="anime--listview">
                <h2 className="anime--gender">{gFilters[index]}</h2>
                <div className="list--itens">{item}</div>
            </div>
        )
    })
   
    return(
        <div className="list--area">
        {AnimesDiv}
        </div>
    )
}


const Section = (props) =>{

        const {animeData, playAnime} = props

        return(
            <section>
               <AnimeListCovers animeData={animeData} playAnime={playAnime} />
            </section>
        )
}

export default Section