import React, { useState } from "react";
import '../playerComponent/playerComponent.css';
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const PlayerPage = (props) => {

    const animeInfo = props.currentAnime
    const episodesAnimeList = animeInfo.seasons[0];
    const animePlayerContent = {
        epNumber: animeInfo.seasons[0].episodes[0].epNumber,
        epTitle: animeInfo.seasons[0].episodes[0].epTitle,
        epPatch: animeInfo.seasons[0].desk + animeInfo.seasons[0].deskEp + animeInfo.seasons[0].episodes[0].patch,
        epThumb: animeInfo.seasons[0].desk + animeInfo.seasons[0].deskThumb + animeInfo.seasons[0].episodes[0].patch
    }

    const [animeEpisodes, setEpisodelist] = useState(episodesAnimeList)
    const [playerAnime, setPlayerAnime] = useState(animePlayerContent)


    const seasonsList = animeInfo.seasons.map((season, index) => {
        return (
            <option key={index} value={index}>{index + 1} Temporada</option>
        )
    })

    const updateEpisodeList = (number) => {

        if (!number) {
            number = 0;
        }

        setEpisodelist(animeInfo.seasons[number])
    }


    const updadePlayerAnime = (animeEpisodeInfo, episode) => {

        setPlayerAnime({
            epNumber: episode.epNumber,
            epTitle: episode.epTitle,
            epPatch: animeEpisodeInfo.desk + animeEpisodeInfo.deskEp + episode.patch,
            epThumb: animeEpisodeInfo.desk + animeEpisodeInfo.deskThumb + episode.patch

        })
    }

    const episodesList = animeEpisodes.episodes.map((episode, index) => {
        return (
            <div className="episode" onClick={() => updadePlayerAnime(animeEpisodes, episode)} key={episode.epNumber}>
                <div title={episode.epTitle} datatype={animeEpisodes.desk + animeEpisodes.deskEp + episode.patch}>
                    <div className="episode-number">{episode.epNumber}</div>
                    <div className="episode-title">{episode.epTitle}</div>
                    <img className="episode-thumb" src={animeEpisodes.desk + animeEpisodes.deskThumb + episode.patch + ".png"} />
                </div>
            </div>
        )
    })

    return (

        <div className="anime-page-background ">
            <div className="anime-page-info">
                <a href="#" onClick={() => { props.setPlayer(false) }} className="close-button"><FontAwesomeIcon className="close-icon" icon={faTimes} /></a>

                <video key={playerAnime.epNumber} width="100%" poster={playerAnime.epThumb + ".png"} controls autoPlay>
                    <source src={playerAnime.epPatch + '.mp4'} type="video/mp4" />
                </video>

                <div className="selected-anime-episode-info">
                    <div className="selected-episode-info">
                        <div className="selected-anime-title">{animeInfo.title}</div>
                        <div className="ratio-info">{animeInfo.ratio}</div>
                        <div className="launch-info">{animeInfo.launch}</div>
                    </div>
                    <div className="episode-title-info">
                        <div className="episode-number">{playerAnime.epNumber}</div><div className="episode-title">{playerAnime.epTitle}</div>
                        <div className="selected-anime-seasons"><select className="selected-season" onChange={() => updateEpisodeList(document.querySelector('.selected-season').selectedIndex)} > {seasonsList} </select></div>
                    </div>
                </div>

                <div className="episodes--list"> {episodesList} </div>
            </div>
        </div>
    )
}

const PlayerComponent = (props) => {

    const { currentAnime, setPlayer } = props

    return (
        <PlayerPage currentAnime={currentAnime} setPlayer={setPlayer} />
    )
}

export default PlayerComponent