import React, {useState, useEffect} from 'react';
import Header from './Header';
import Section from './Section';
import Footer from './Footer';
import AnimeDB from './AnimeDB';
import PlayerComponent from './playerComponent';

const App = () => {

    const [animeData] = useState(AnimeDB)
    const [playerOpen, setPlayer] = useState(false)
    const [blackHeader, setBlackHeader] = useState(false);
    
    const animeToPlay = {
        id: null,
        tag: '',
        title: '',
        genre: '',
        ratio: '',
        launch: '',
        description: '',
        seasons: [
            {
                desk: '',
                deskEp: '',
                deskThumb: '',
                deskCover: '',
                deskBackground: '',
                episodes: [
                    {
                        epNumber: '',
                        epTitle: '',
                        patch: '',
                    }
                ]
            },
        ]
    }

    const [currentAnime, setCurrentAnime] = useState(animeToPlay)
    
    const playAnime =(anime) =>{

        setPlayer(true)

        setCurrentAnime({
            id: anime.id,
            tag: anime.tag,
            title: anime.title,
            cover: anime.cover,
            background: anime.background,
            genre: anime.genre,
            ratio: anime.ratio,
            launch: anime.launch,
            description: anime.description,
            seasons: anime.seasons
        })
    }

    useEffect(() => {
        const scrollListener = () => {

            if (window.scrollY > 10) {
                setBlackHeader(true);
            } else {
                setBlackHeader(false);
            }
        }

        window.addEventListener('scroll', scrollListener);

        return () => {
            window.removeEventListener('scroll', scrollListener)
        }
    }, [])

    return (
        <div className={`page ${ playerOpen ? "scroll--hiden" : ""}`}>
            <Header animeData={animeData} playAnime={playAnime} blackHeader={blackHeader} />
            {playerOpen ? <PlayerComponent currentAnime={currentAnime} setPlayer={setPlayer}/> : ""}  
            <Section animeData={animeData} playAnime={playAnime}/>
            <Footer />
        </div>
    )
}



export default App